}
.header__logo:hover, .header__logo-cn:hover {
  opacity: 0.7;
}
.header__logo {
  width: 200px;
  height: 41px;
}
@media only screen and (max-width: 575px) {
  .header__logo {
    width: 100px;
    height: 20px;
  }
}
.header__logo-cn {
  width: 113px;
  height: 22px;
}
@media only screen and (max-width: 575px) {
  .header__logo-cn {
    width: 102px;
    height: 20px;
  }
}
.header__logo-cn svg {
  width: 113px;
  height: 22px;
}
@media only screen and (max-width: 575px) {
  .header__logo-cn svg {
    width: 102px;
    height: 20px;
  }
}
.header__home {
  font-size: 20px;
