@media only screen and (max-width: 575px) {
  .header__home {
    order: 1;
    width: 100%;
    text-align: center;
  }
}
.header__home:hover {
  color: rgba(51, 51, 51, 0.7);
}

.footer {
  position: relative;
  z-index: 10;
  padding: 10px 0 20px;
}
.footer__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}
.footer__logo {
  width: 164px;
  height: 33px;
  transition: 0.3s ease;
}
@media only screen and (max-width: 575px) {
  .footer__logo {
    width: 120px;
    height: 24px;
  }
}
.footer__logo:hover {
  opacity: 0.7;
}
.footer__home {
  font-weight: 700;
  font-size: 20px;
  text-transform: uppercase;
  transition: 0.3s ease;
}
.footer__home:hover {
  color: rgba(51, 51, 51, 0.7);
}
.footer__up {
  width: 37px;
