}
.hero--article .hero__title {
  font-weight: 400;
  font-size: 45px;
  line-height: 1.11;
  text-align: left;
}
@media only screen and (max-width: 1279px) {
  .hero--article .hero__title {
    font-size: calc(40px + (45 - 40) * (100vw - 992px) / (1280 - 992));
  }
}
@media only screen and (max-width: 991px) {
  .hero--article .hero__title {
    font-size: calc(35px + (40 - 35) * (100vw - 768px) / (992 - 768));
  }
}
@media only screen and (max-width: 767px) {
  .hero--article .hero__title {
    font-size: calc(28px + (35 - 28) * (100vw - 0px) / (768 - 0));
  }
}
.hero--article .hero__text {
  max-width: 100%;
  margin: 38px 0 0;
