.article {
	&__main {
		display: flex;
		gap: 40px;
		margin-top: 62px;
		padding-bottom: 80px;

		@include max(ti) {
			flex-direction: column;
			padding-bottom: 40px;
		}
	}

	&__banners {
		position: sticky;
		top: 20px;
		display: flex;
		flex-direction: column;
		gap: 25px;
	}

	@include max(ti) {
		margin-top: 30px;
	}

	&__content {
		h1,
		h2,
		h3,
		h4 {
			color: $black;
			margin-top: 25px;
			margin-bottom: 15px;
		}

		h1 {
			color: $color-title;
			font-weight: 700;
			font-size: 40px;
			line-height: 1.3;
			margin-top: 40px;
			margin-bottom: 40px;

			@include max(ti) {
				font-size: 32px;
				margin-top: 30px;
				margin-bottom: 30px;
			}

			@include max(t) {
				font-size: 28px;
				margin-top: 20px;
				margin-bottom: 20px;
			}
		}

		h2 {
			color: $color-title;
			font-weight: 700;
			font-size: 35px;
			line-height: 1.28;
			margin-top: 40px;
			margin-bottom: 40px;

			@include max(ti) {
				font-size: 30px;
				margin-top: 30px;
				margin-bottom: 30px;
			}

			@include max(t) {
				font-size: 25px;
				margin-top: 20px;
				margin-bottom: 20px;
			}
		}

		h3 {
			color: $color-h3;
			font-weight: 700;
			font-size: 24px;
			margin-top: 20px;
			margin-bottom: 10px;

			@include max(ti) {
				font-size: 22px;
			}

			@include max(t) {
				font-size: 20px;
				margin-top: 10px;
				margin-bottom: 5px;
			}
		}

		p:not(.popup_img) {
			color: $black;
			font-size: 20px;
			line-height: 1.16;
			margin-bottom: 10px;

			@include max(tl) {
				font-size: 18px;
			}

			@include max(ti) {
				font-size: 16px;
			}

			@include max(t) {
				font-size: 14px;
			}
		}

		a {
			color: $black;
			text-decoration: underline;

			&:hover {
				text-decoration: none;
			}
		}

		i {
			color: $black;
		}

		table {
			width: 100%;
			border-collapse: collapse;
			margin-bottom: 20px;
			word-break: break-word;

			td {
				border: 1px solid rgba($black, 0.7);
				padding: 8px 10px;

				@include max(t) {
					font-size: 14px;
				}
			}
		}

		.text-note {
			position: relative;
			padding: 48px 80px 58px;
			background-color: $color-background;
			border-radius: 40px;
			margin: 30px 0;

			@include max(tl) {
				padding: 35px 70px 50px;
			}

			@include max(ti) {
				padding: 35px 58px 50px;
			}

			@include max(t) {
				padding: 50px 20px 55px;
			}

			&::before,
			&::after {
				position: absolute;
				content: "";
				display: block;
				width: 45px;
				height: 45px;
				background-repeat: no-repeat;
				background-image: url('data:image/svg+xml,<svg width="45" height="45" viewBox="0 0 45 45" fill="none" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"><g opacity="0.9"><mask id="mask0_299_19" style="mask-type:alpha" maskUnits="userSpaceOnUse" x="0" y="0" width="45" height="45"><rect x="45" y="45" width="45" height="45" rx="10" transform="rotate(-180 45 45)" fill="url(%23pattern0_299_19)"/></mask><g mask="url(%23mask0_299_19)"><rect x="45" y="45" width="55.7227" height="47.5488" rx="10" transform="rotate(-180 45 45)" fill="url(%23paint0_radial_299_19)"/></g></g><defs><pattern id="pattern0_299_19" patternContentUnits="objectBoundingBox" width="1" height="1"><use xlink:href="%23image0_299_19" transform="scale(0.00195312)"/></pattern><radialGradient id="paint0_radial_299_19" cx="0" cy="0" r="1" gradientUnits="userSpaceOnUse" gradientTransform="translate(62.8777 54.8734) rotate(43.3463) scale(36.7957 67.973)"><stop stop-color="%23448ECA"/><stop offset="1" stop-color="%2328376D"/></radialGradient><image id="image0_299_19" width="512" height="512" xlink:href="data:image/png;base64,"/></defs></svg>');

				@include max(ti) {
					width: 33px;
					height: 33px;
					background-image: url('data:image/svg+xml,<svg width="33" height="33" viewBox="0 0 45 45" fill="none" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"><g opacity="0.9"><mask id="mask0_299_19" style="mask-type:alpha" maskUnits="userSpaceOnUse" x="0" y="0" width="45" height="45"><rect x="45" y="45" width="45" height="45" rx="10" transform="rotate(-180 45 45)" fill="url(%23pattern0_299_19)"/></mask><g mask="url(%23mask0_299_19)"><rect x="45" y="45" width="55.7227" height="47.5488" rx="10" transform="rotate(-180 45 45)" fill="url(%23paint0_radial_299_19)"/></g></g><defs><pattern id="pattern0_299_19" patternContentUnits="objectBoundingBox" width="1" height="1"><use xlink:href="%23image0_299_19" transform="scale(0.00195312)"/></pattern><radialGradient id="paint0_radial_299_19" cx="0" cy="0" r="1" gradientUnits="userSpaceOnUse" gradientTransform="translate(62.8777 54.8734) rotate(43.3463) scale(36.7957 67.973)"><stop stop-color="%23448ECA"/><stop offset="1" stop-color="%2328376D"/></radialGradient><image id="image0_299_19" width="512" height="512" xlink:href="data:image/png;base64,"/></defs></svg>');
				}
			}

			&::before {
				top: 30px;
				left: 30px;

				@include max(tl) {
					top: 20px;
					left: 20px;
				}

				@include max(t) {
					top: 10px;
					left: 10px;
				}
			}

			&::after {
				bottom: 30px;
				right: 30px;
				transform: rotate(180deg);

				@include max(tl) {
					bottom: 20px;
					right: 20px;
				}

				@include max(t) {
					bottom: 10px;
					right: 10px;
				}
			}

			a {
				color: $black;
				text-decoration: underline;

				&:hover {
					text-decoration: none;
				}
			}

			h1,
			h2,
			h3,
			h4,
			p,
			span,
			div {
				color: $black !important;
				background-color: unset !important;
				text-align: center;
			}

			h2 {
				margin-top: 0;
				margin-bottom: 20px;

				&:empty {
					margin: 0;
				}
			}

			p {
				font-size: 24px;
				line-height: 1.29;
				text-align: center;
				font-style: italic;
				margin: 0;

				@include max(ti) {
					font-size: 22px;
				}

				@include max(t) {
					font-size: 20px;
				}

				& + p:not(:empty) {
					margin-top: 10px;
				}

				& + div:not(:empty) {
					margin-top: 10px;
				}
			}

			ul {
				margin: 0;
				padding: 0;
				list-style-position: inside;
			}
		}

		.opinion {
			display: flex;
			gap: 20px;
			margin: 40px 0;

			@include max(ti) {
				margin: 20px 0;
			}

			@include max(t) {
				flex-direction: column;
				align-items: center;
				gap: 10px;
			}

			p {
				color: $black !important;

				@include max(t) {
					text-align: center;
				}

				&:first-child {
					color: $black !important;
					font-size: 20px;

					@include max(t) {
						font-size: 18px;
					}
				}

				span {
					color: $black !important;
				}

				i {
					font-style: normal;
					color: $black !important;

					strong {
						color: $black !important;
					}
				}
			}

			.o-photo-cn {
				display: inline-block;
				min-width: 240px;
				max-width: 240px;
				height: 240px;
				border-radius: 20px;
				overflow: hidden;

				p {
					height: 100%;
					margin: 0;
				}

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}

			h1,
			h2,
			h3,
			h4,
			p,
			span {
				color: $white !important;
				background-color: unset !important;
			}
		}

		.image {
			margin-top: 30px;
			margin-bottom: 30px;

			.img-container {
				position: relative;
				border-radius: 30px;
				overflow: hidden;

				.popup_img {
					display: block;
					width: 100%;
				}
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				margin: auto;
			}

			figcaption {
				color: $black;
				font-size: 14px;
				text-align: center;
				margin: 8px 0 20px;

				@include max(t) {
					font-size: 12px;
				}
			}

			.img-source {
				position: absolute;
				bottom: 10px;
				right: 20px;

				&:hover span:not(:empty) {
					background-color: rgba(0, 0, 0, 0.3);
				}

				span:not(:empty) {
					display: inline-block;
					background-color: rgba(0, 0, 0, 0.6);
					padding: 5px 20px;
					border-radius: 3px;
					transition: background-color 0.3s ease;
					color: $white;
					font-size: 12px;
					text-align: center;
				}

				a {
					color: #fff;
					text-decoration: none;
				}
			}
		}

		.subtitle-with-picture {
			display: flex;
			align-items: center;
			gap: 20px;

			&__image {
				min-width: 100px;
				max-width: 100px;
				height: 100px;
				border-radius: 50%;
				overflow: hidden;
				margin-left: -120px;

				@include max(d) {
					margin-left: 0;
				}

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}

		ul {
			list-style: none;
			padding: inherit;
			margin: inherit;

			li {
				position: relative;
				padding-left: 18px;
				font-size: 20px;
				line-height: 1.16;

				@include max(tl) {
					font-size: 18px;
				}

				@include max(ti) {
					font-size: 16px;
				}

				@include max(t) {
					padding-left: 13px;
					font-size: 14px;
				}

				&::before {
					position: absolute;
					top: 8px;
					left: 0;
					content: "";
					display: block;
					min-width: 8px;
					max-width: 8px;
					height: 8px;
					border-radius: 50%;
					background: radial-gradient(53.12% 137.82% at 32.08% 20.76%, #448ECA 0%, #28376D 100%);

					@include max(tl) {
						top: 6px;
					}

					@include max(ti) {
						top: 5px;
					}

					@include max(t) {
						min-width: 6px;
						max-width: 6px;
						height: 6px;
					}
				}

				& + li {
					margin-top: 8px;
				}
			}
		}
	}
}
